.joke {
    padding: 5px;
    border-bottom: 1px solid orange;
    color: #333;
    font-size: 1.3em;
    cursor: pointer;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.joke:hover {
    opacity: 0.7;
}


