.category {
    padding: 5px 0;
    border-bottom: 1px solid #777;
    text-align: center;
    cursor: pointer;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.category:hover {
    opacity: .7;
}

.category:active {
    -webkit-transform: scale(.9);
        -ms-transform: scale(.9);
            transform: scale(.9);
}


