.content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: start;
      align-items: flex-start;
  padding: 10px;
  border-radius: 5px;
  overflow: hidden;
  -webkit-box-shadow: 0 0 10px 5px rgba(0, 0, 0, .7);
          box-shadow: 0 0 10px 5px rgba(0, 0, 0, .7);
  background: #eee;
}

.main {
  width: calc(100% - 300px);
  max-height: calc(100vh - 105px);
  overflow-y: auto;
  overflow-x: hidden;
}

.sidebar {
  width: 300px;
}

@media (max-width: 600px) {
  .content {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }
  .main {
    width: 100%;
    margin-bottom: 20px;
  }
  .sidebar {
    width: 100%;
  }
}


